body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100%;
  font-family: 'Pathway Gothic One', sans-serif;
}

.App-header {
  width: 100%;
  min-height: 100vh;
}

a:link, a:visited, a:active {
  color:var(--green);
  text-decoration: none;
}

a:hover {
  color: red;
}

@media screen and (max-width: 800px) {
  .App-header {
    margin-bottom: 75vh;
  }
}
.navBar {
    text-transform: uppercase;
    letter-spacing: 2px;
    font-family: 'Spinnaker', sans-serif;
    color: linen;
    font-size: 3vw;
    font-weight: bold;
    margin: 0px 0px 10vh -10px;
    padding-left: 7%;
    padding-top: 20px;
    padding-bottom: 15px;
    position: fixed;
    z-index: 1;
    width: 92.5%;
    top: 0;
}

#link{
    color: rgb(221, 218, 6);
    text-decoration: none;
}

NavLink:hover {
    color: white;
}

#leftName {
    color: rgb(221, 218, 6);
    text-transform: uppercase;
    letter-spacing: 2px;
    font-family: 'Spinnaker', sans-serif;
    font-size: 12px;
    font-weight: bold;
    margin-right:100%;
    padding-top: 20px;
    position: fixed;
    z-index: 1;
    top: 0;
    float: left;

}

.topNav {
    float: right;
    padding: 0% 5% 0% 0%;
    font-size: 12px;
    margin-bottom: 10vh;
    
}
  
.navBar a:hover {
    color: white;
}

.dropNav {
    float: right;
    overflow: hidden;
    display: none;
    margin-right: 2vw;
}

#dropbtn {
    background-color: transparent;
    font-size: 20px;
    color:var(--green);
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: transparent;
    min-width: 160px;
    z-index: 1;
    text-align: right;
}

.dropNav:hover .dropdown-content {
    display: block;
    margin-left: -130px;
}

.min-screen {
    float: none;
    color: black;
    font-weight: normal;
    padding: 5px 0px;
    text-decoration: none;
    display: block;
    text-align: right;
    font-size: 13px;
}


@media screen and (max-width: 800px) {
    .dropNav {
        display: block;
    }
    
    .topNav {
        display: none;
    }
    #leftName{
        display: none;
    }
  }
/* Fixed/sticky icon bar (vertically aligned 50% from the top of the screen) */
.icon-bar {
    position: fixed;
    top: 50%;
    z-index: 10;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  
  /* Style the icon bar links */
  .icon-bar a {
    display: block;
    text-align: center;
    padding: 16px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    color: white;
    font-size: 15px;
  }
  
  /* Style the social media icons with color, if you want */
  .icon-bar a:hover {
    background-color: rgb(223, 211, 211);
  }
  
  .email {
    background: #55acee;
    color: white;
  }
  
  #phone {
    background: rgb(126, 148, 1);
    color:white;
  }
  
  .linkedin {
    background: #007bb5;
    color:white;
  }
  
  .github {
    background: rgb(221, 218, 6);
    color: white;
  }

#whoAmI {
  width: 100% !important;
  overflow-x: hidden !important;
  background-color: var(--darkblue);
  margin-bottom: 5vh;
}

#whoAmI h2 {
  font-family: 'Spinnaker', sans-serif;
  color: white;
  text-transform: uppercase;
  font-size: 2.75vh;
  margin-left: 13%;
  align-self: left;
  align-items: left;
  justify-content: left;
}

#whoAmI p {
  width: 50%;
