:root{
  --darkblue: #0a024b;
  --green: rgb(221, 218, 6);

}
#welcome-page {
  min-height: 100vh;
  width: 100% !important;
  overflow-x: hidden !important;
}

.welcomeHeader {
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 5vw;
    text-transform: lowercase;
    font-weight: bold;
    letter-spacing: .15em;
}
  
.typeWrite {
  font-family: 'Damion', cursive;
  color:linen;
  overflow: hidden;
  white-space: nowrap;
  letter-spacing: .15em;
  -webkit-animation: 
    typing 3.5s steps(50, end);
          animation: 
    typing 3.5s steps(50, end);
}
.welcome{
  font-family: 'Spinnaker', sans-serif;
  text-transform: uppercase;
  color: linen;
  font-size: 3vw;
}

@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
  
.top {
    background-color:#0a024b;
    background-color:var(--darkblue);
    padding: 10% 0% .5% 0%;
