    height: 50%;
}
  
.bottom {
    padding: 0% 25% 0% 10%;
    justify-content: center;
    align-items: center;
    display: flex;
}
  
.exploreButton {
    position: absolute;
    border: 2px solid black;
    text-transform: uppercase;
    letter-spacing: .15em;
    font-weight: bold;
    padding: 1.1%;
    font-size: 2vw;
}




@media screen and (min-width: 1100px) {
